'use client'
import Header from "../../../../pages/Header";
import Nav from "../../../../components/Home/Nav";
import "../../../styles/main.scss"
import Search from "../../../../components/Home/Search";
import BlogPage from "../../../../components/Blog/BlogPage";
import Card from "../../../../components/ui/card/NewsCard";
import { useIsCustomView } from "../../../../hooks/useIsCustomView";
import SmallInfoHorizontal from "../../../../components/Home/SmallInfo-horizontal"
import BlogTitle from "../../../../components/Blog/BlogTitle"
import BlogSapo from "../../../../components/Blog/BlogSapo"
import BlogHeading from "../../../../components/Blog/BlogHeading"
import BlogContent from "../../../../components/Blog/BlogContent"
import BlogQuote from "../../../../components/Blog/BlogQuote"
import SimpleCard from "../../../../components/ui/card/NoTextCard"
import HeaderDesktop from "../../../../pages/HeaderDesktop";
import { use, useEffect, useState } from "react";
import { useMediaQuery } from "react-responsive";
import BlogButton from "@/components/Blog/BlogButton";
import CommentInput from "@/components/Blog/CommentInput";
import Comments from "@/components/Blog/Comments";
interface Data {
    author: string,
    body: string,
    created_at: string,
    head: HeadData,
    id: number,
    path: string,
    published_text: string,
    source_url: string,
    title: string,
    updated_at: string,

}
export interface SeoData {
    title: string;
    description: string;
    keywords: string;
    canonical: string;
    robots: string;
    language: string;
}

export interface HeadData {
    seo: SeoData;
}

interface PageProps {
    params: Promise<{ id: string }>;
}
interface List {
    id: number;
    title: string;
    summary: string;
    thumbnail: string;
    article_url: string;
    category: string;
    official: boolean;
    time: string;
    name: string;
    avatar: string;
}
const Blog = ({ params }: PageProps) => {
    const [list, setList] = useState<List[]>([]);
    const [data, setData] = useState<Data>();
    const { id } = use(params);
    console.log(id);
    const getList = async () => {
        try {
            const res = await fetch("https://webthethao.wepro.io.vn/api/newfeed?page=1");
            const data = await res.json();
            console.log(id)
            data.items.filter((item: List) => item.id === Number(id))
            setList(data.items);
            console.log("id", data.items);

        } catch (error) {
            console.log(error);
        }
    }

    const getData = async (index: number) => {
        try {
            const res = await fetch("https://webthethao.wepro.io.vn/api/article/bong-da-viet-nam/afc-gui-thong-diep-u23-viet-nam-khong-phai-doi-thu-de-choi-tai-chau-a-Et73kskRR.htm");
            const data1 = await res.json();
            console.log(data1);
            setData(data1);

        } catch (error) {
            console.log(error);
        }
    }
    useEffect(() => {
        getData();
        getList();

    }, []);
    const changeHeader = useMediaQuery({ query: "(max-width: 1024px)" })
    const changeNav = useMediaQuery({ query: "(max-width: 768px)" })
    const card1 =
    {
        avatar: "/assets/Rectangle 1.webp",
        name: "Kiet Phan",
        time: "2 giờ trước",
        image: "/assets/image.webp",
        title: "Adidas trở lại mạnh mẽ trên thị trường Pickleball với dòng vợt AdiPower",
        content: "Adidas đang định hình lại vị thế của mình trong làng Pickleball thông qua dòng vợt AdiPower mới - sự kết hợp giữa chất lượng cao và giá cả hợp lý.",
        category: "Pickleball",
        official: true
    }
    return (
        <div className='App'>
            {changeHeader ? <Header /> : <HeaderDesktop />}
            <main>
                <div className="no-gap">
                    <Search />
                    <div className="blog">
                        <div className="blog__container">
                            <div className="blog__seo">{data?.head.seo.title}</div>
                            <img src="/assets/image.webp" alt="" className="blog__image w-full" />
                            <div className="blog__user">

                                <div className="blog__user--inner">
                                    <SmallInfoHorizontal avatar="/assets/Rectangle 1.webp" name={data?.author} time={data?.created_at} official={true} />

                                    <div className="blog__cat">
                                        Pickleball
                                    </div>
                                </div>

                            </div>
                        </div>
                        <div className="blog__body">
                            <BlogTitle title={data?.title} />
                            <div dangerouslySetInnerHTML={{ __html: data?.body }}></div>
                            {/* <blockquote><p>Mua các sản phẩm mới nhất tại <a href="">webthethao.vn</a></p><cite>-Webthethao</cite></blockquote> */}

                            {/* <img src="/assets/image.webp" alt="" className="blog__image--inner w-full" />
                <div className="blog__body">
                    <BlogHeading heading={heading2} />
                    <BlogQuote quote={quote.quote} name={quote.name} />
                    <BlogContent content={content4} /> */}
                            <div className="blog__btn-container">
                                <BlogButton number={2} text="Bình luận" />
                                <BlogButton number={10} text="Chia sẻ" />
                            </div>
                        </div>
                        <div className="blog__comment">
                            <CommentInput />
                            <Comments avatar="/assets/Image (3).webp" name="Huong Nguyen" content="Anna hướng dẫn chi tiết, sửa tư thế nhẹ nhàng, giúp tôi thấy cơ thể dẻo dai hơn chỉ sau 2 tháng." time="10 phút trước" />
                        </div>
                    </div>
                </div>
                <Card avatar={card1.avatar} name={card1.name} time={card1.time} image={card1.image} title={card1.title} content={card1.content} category={card1.category} official={card1.official} link="/blog" />
                <Card avatar={card1.avatar} name={card1.name} time={card1.time} image={card1.image} title={card1.title} content={card1.content} category={card1.category} official={card1.official} link="/blog" />
            </main>
            {changeNav ? <Nav /> : ""}
        </div>

    )
}
export default Blog;
